<template>
  <me-navbar class="header"  v-if="visible">
    <i class="iconfont iconsaoyisao" slot="left"></i>
    <!-- <div slot="center">搜索框</div> -->
    <me-search-box
      placeholder="开学季有礼，好货5折起"
      slot="center"
      @query="getQuery"
      fake
      @click.native="goToSearch"
    ></me-search-box>
    <!-- @click.native="goToSearch"不是原生的标签触发点击事件都要加 .native  表示原生的事件 -->
    <i class="iconfont iconxiaoxi" slot="right"></i>
  </me-navbar>
</template>
  
<script>
  import MeNavbar from 'base/navbar';
  import MeSearchBox from 'base/search-box';

  export default {
    name: 'HomeHeader',
    components:{
      MeNavbar,
      MeSearchBox
    },
    data(){
      return{
        visible:true
      }
    },
    methods:{
      show(){
        this.visible=true;
      },
      hide(){
        this.visible=false;
      },
      getQuery(query){
        console.log(query)
      },
      goToSearch(){
        this.$router.push('/search')//把地址添加进来
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '~assets/scss/mixins';

  .header {
    &.mine-navbar {
      background-color: transparent;
      transition: background-color 0.5;
      // background-color: orange;
    };

    &.header-transition{
      background-color: $header-bgc-translucent;
    }
    
    .iconfont {
      color: $icon-color-default;
      font-size: $icon-font-size;
    }
  }

</style>



