<template>
    <!-- 方向    是否无缝滚动    是否自动轮播    是否有分页器 -->
    <div class="slider-wrapper">
      <me-loading v-if="!sliders.length"></me-loading>
      <!-- 轮播图容器 -->
      <!-- 为什么这里的属性要单独传，而不是作为一个对象传:
      1,语义化更明显。
      2,在slider基础组件内部的props里的validator可以对所传的属性进行校验，用对象传递 是无法校验的 -->
      <me-slider
        :data="sliders"
        :direction="direction"
        :loop="loop"
        :interval="interval"
        :pagination="pagination"
        v-else
      >
      <!-- 异步获取数据要很长时间       v-show是先渲染再隐藏    v-if为false的话根本不会出现-->
      <!-- 数据获取之前就已经调用了基础的slider组件，初始化之后是空的所有里面的计算会出错，因为没有给他填充数据
      解决办法：等有了数据之后在开始渲染这里用  v-if="sliders.length"  判断数组的长度有值再开始渲染 -->
        <swiper-slide
          v-for="(item,index) in sliders"
          :key="index"
        >
          <a :href="item.linkUrl" class="slider-link">
            <img :src="item.picUrl" alt="" class="slider-img">
          </a>
        </swiper-slide>
      </me-slider>
    </div>
</template>
  
<script>
  import MeSlider from 'base/slider';
  import {swiperSlide} from 'vue-awesome-swiper';
  import {sliderOptions} from './config';
  import {getHomeSlider} from 'api/home'
  import MeLoading from 'base/loading';
  
  export default {
    name: 'HomeSlider',
    components: {
      MeSlider,
      swiperSlide,
      MeLoading
    },
    data() {
      return {
        direction: sliderOptions.direction,
        loop: sliderOptions.loop,
        interval: sliderOptions.interval,
        pagination: sliderOptions.pagination,
        sliders: []
      };
    },
    created(){
      this.getSliders();
    },
    methods:{
      //API
      update() {
        return this.getSliders();
      },

      getSliders() {
        // 获取数组  赋值
        return getHomeSlider().then(data=>{
          this.sliders=data;
        });
      }
    }
  };
</script>
  
<style lang="scss" scoped>
  .slider-wrapper{
    height: 183px;
  }

  .slider-link{
    display: block;
  }
  
  .slider-link,
  .slider-img{
    width: 100%;
    height: 100%;
  }
</style>


