import axios from 'axios';
import jsonp from 'assets/js/jsonp';
import {
  SUCC_CODE,
  TIMEOUT,
  HOME_RECOMMEND_PAGE_SIZE,
  jsonpOption
} from 'api/congif';

const shuffle = (arr) => {
  const arrLength = arr.length;
  let i = arrLength;
  let rndNum;


//因为数据请求过来就是按照顺序去显示的，而老师想要打乱数据让它不按照顺序去显示。
//即让arr中元素的顺序打乱。下面定义一个索引 i ，初始值为这个arr数组的长度，即最后一个元素的索引。
//在循环中，i一直是有规则的变化，即每一次减去1. 那么就从倒数第一个依次获取了数组中的元素。
//只有当随机数与当前索引i值不一样，例如i为5，随机数为2，这样索引为5的元素和索引为2的元素交换位置，才可以打乱数据顺序哦
  while (i--) {
    if (i !== (rndNum = Math.floor(Math.random() * arrLength))) {//i的长度减一次比较一次是否和arrLength长度的随机数相等
      [arr[i], arr[rndNum]] = [arr[rndNum], arr[i]];//调换位置
    }
  }

  return arr;
};

// 获取幻灯片数据 axios
export const getHomeSlider = () => {
  return axios
    .get('http://www.imooc.com/api/home/slider', {
      timeout: TIMEOUT //只要是发送数据都要写一个timeout
    })
    .then(res => {
      // console.log(res.data.slider)
      // 判断code的值等于0   直接返回res下的data里的slider的值
      if (res.data.code === SUCC_CODE) {
        let sliders = res.data.slider;
        const slider = [sliders[Math.floor(Math.random() * sliders.length)]]; //随机取一图片放在数组里
        sliders = shuffle(sliders.filter(() => Math.random() >= 0.5));//filetr遍历根据条件筛选 
        if (sliders.length === 0) {
          sliders = slider;
        }
        return sliders;
      }
      // 抛出错误
      throw new Error('没有成功获取到数据！');
    })
    .catch(err => {
      //catch捕获错误
      if (err) {
        //判断一下err   如果有这个错误就打印出来
        console.log(err);
      }
      return [
        //出现错误返回的数据
        {
          linkUrl: 'https://www.imooc.com',
          picUrl: require('assets/img/404.png')
        }
      ];
      //只要没有在catch里手动抛出错误   后面就可以用then连下去
    })
    // .then(res => {
    //   return new Promise(resolve => {
    //     //一秒钟之后在把数据传过去
    //     setTimeout(() => {
    //       resolve(res);
    //     }, 1000);
    //   });
    // });
};

//获取热门的推荐数据-jsonp
export const getHomeRecommend = (
  page = 1,
  psize = HOME_RECOMMEND_PAGE_SIZE
) => {
  const url = 'https://ju.taobao.com/json/tg/ajaxGetItemsV2.json';
  const params = {
    page,
    psize,
    type: 0,
    frontCatId: ''
  };
  return jsonp(url, params, jsonpOption)
    .then(res => {
      if (res.code === '200') {
        return res;
      }

      throw new Error('没有成功获取导数据！');
    })
    .catch(err => {
      if (err) {
        console.log(err);
      }
    })
    // .then(data => {
    //   return new Promise(resolve => {
    //     setTimeout(() => {
    //       resolve(data);
    //     }, 1000);
    //   });
    // });
};



