<template>
  <swiper :options="swiperOption" :key="keyId">
    <slot></slot>
      <div class="swiper-pagination" v-if="pagination" slot="pagination"></div>
  </swiper>
</template>
  
<script>
  import {swiper} from 'vue-awesome-swiper';
  export default {
    name: 'MeSlider',
    components: {
      swiper
    },
    props: {      //在props里面default是默认值的意思    type是指定接收值的类型
      //图片滑动方向
      direction: {
        type: String,
        default: 'horizontal',
        //validator方法是用来验证传递过来的value参数的值是否符合要求
        //可以将validator理解成是vue中props自带验证器
        validator(value) {
          //当返回值>-1,说明验证通过，返回的值要么是horizontal', 要么是' vertical'。返回就位true 
          return [
            'horizontal',
            'vartical'
          ].indexOf(value) > -1;
        }
      },
      //自动轮播的间隔时间
      interval: {
        type: Number,
        default: 3000,
        validator(value) {
          return value >= 0;
        }
      },
      // 是否无缝滚动
      loop: {
        type: Boolean,
        default : true
      },
      //是否有分页器
      pagination: {
        type: Boolean,
        default: true
      },
      data: {
        type:Array,
        default(){
          return [];
        }
      }
    },
    // this其实是指向当前的vue实例， 所以可以获取到vue实例data中的数据， 
    //因为props也是vue实例下的一个属性， 所以可以通过this获取到props下的数据
    data(){
      return {
        keyId:Math.random()
      }
    },
    watch:{
      data(newData){
        if(newData.length === 0){
          return;
        }
        this.swiperOption.loop=newData.length === 1 ? false : this.loop;
        this.keyId=Math.random()
      }
    },
    created(){
      this.init();
    },
    methods:{
      init() {
        this.swiperOption= {
          watchOverflow: true,  //如果只有一张图片 swiper就会失效
          direction: this.direction,  //就是传过来的direction
      //自动轮播 ：是否开启 判断为true开启 {延迟多少 延迟就是this.interval ， 有交互停止自动轮播 } 判断false  不开启自动轮播
          autoplay: this.interval ? { delay: this.interval, disableOnInteraction: false  } : false, 
          slidesPerView: 1,  //设置slider容器能够同时显示的slider数量  也就是能同时显示几张图片
          loop: this.data.length <= 1 ? false : this.loop, //开不开启无缝滚动
          pagination: {  //是否开启分页器
            el: this.pagination ? '.swiper-pagination' : null
          }
        }
      }
    }
  };
</script>
  
<style lang="scss" scoped>
  .swiper-container {
    width: 100%;
    height: 100%;
  }
</style>


